.navbar {
    display: -ms-flexbox;
    display: flex;
    place-content: center;
    background-color: rgba(255, 165, 0, 0.5);
    height: 15vh;
    cursor: pointer;
}
.navbar > div {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 30px;
    height: 100%;
}
.navbar > div:hover {
    background-color: orange;
}
.navbar > div > span {
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    -ms-flex-item-align: center;
        align-self: center;
}



